import React, { useState, useContext, useEffect, useRef, useMemo, useCallback } from "react";
import s from "./Task9.module.css";
import { Context } from "../../Context";
import { HeadingText } from "../HeadingText/HeadingText";
import { DragDropContext, Droppable, Draggable } from 'react-beautiful-dnd';

import coco from "./img/coco.svg"
import kiwi from "./img/kiwi.svg"

// Контент модального окна - Алфавит
const AlphabetModalContent = () => {
  return (
    <>
      <h3> Словать</h3>
      <br />
      <p>Много разных слов</p>
    </>
  );
};

// Контент модального окна - Помощь
const HelpModalContent = () => {
  return (
    <>
      <h3>Электронный помощник</h3>
      <br />
      <p></p>
    </>
  );
};

// Изменение заголовка (поддерживаеи html тэги)
// START
// Верхний заголовок - название урока ( <> текст размещать внутри тэгов </> )
const LessonHeading = () => (
  <>
    <p>Parrots dialogue (in progress)</p>
  </>
);
// Заголовок с заданием к уроку ( <> текст размещать внутри тэгов </> )
const ThemeHeading = () => (
  <>
    <p style={{ textAlign: "center" }}></p>
  </>
);
// Текс title с переводом (НЕ поддерживает html тэги)
// const titleHeading = "Установите буквы в правильном порядке.";

const parrotsPhrases = [
  {
    data: 'hello',
    phrase: 'Hello!'
  },
  {
    data: 'hello',
    phrase: 'Hello!'
  },
  {
    data: 'howareyou',
    phrase: 'How are you?'
  },
  {
    data: 'finethankyou',
    phrase: 'I’m fine! Thank you.'
  },
];

function Task9() {
  const {
    helpSetIcon,
    alphabetSetIcon,
    helpSetModalContent,
    alphabetSetModalContent,
    lessonSetStartAction,
    lessonSetFinishAction,
    lessonSetResetTask,
    resetTask,
    lessonSetCheckTask,
    checkTask,
    lessonSetAndAnswer,
    lessonSetViewFeedback,
  } = useContext(Context);

  // Иконки правого поля  - (помощь и алфавит)
  useEffect(() => {
    //  helpSetIcon - иконка электронного помощника (включить - true, отключить - false)
    helpSetIcon(true);
    //  alphabetSetIcon - иконка алфавита (включить - true, отключить - false)
    alphabetSetIcon(true);
    helpSetModalContent(<HelpModalContent />);
    alphabetSetModalContent(<AlphabetModalContent />);
    setItems(parrotsPhrases.sort(() => Math.random() - 0.5));

    // listWrapper.current.style.width = `${listWrapper.current.clientWidth}px`;
  }, []);
  const [phrases, setItems] = useState([]);
  console.log(phrases)
  useEffect(() => {
    if (resetTask === true) {

    }
    lessonSetResetTask(false);
  }, [resetTask]);

  useEffect(() => {
    if (checkTask === true) {

    }
    lessonSetCheckTask(false);
  }, [checkTask]);



  //  Внесение изменений в контент задания
  return (
    <div className="lessonContent">
      <HeadingText
        lessonHeading={<LessonHeading />}
        themeHeading={<ThemeHeading />}
        // titleHeading={titleHeading}
        // soundSource={soundSource}
        soundVisible={true}
        titleVisible={true}
      />
      <div className={s.wrapper}>
        <div className={s.parrotsDialogue}>
          <div className={s.parrotCoco}><img src={coco} className={s.coco}></img></div>
          <div className={s.cocoPhrases}>
            <div className={[s.dialogueParrotPhrase, s.dialogueParrotPhraseActive].join(' ')}>
              <p></p>
            </div>
            <div className={[s.dialogueParrotPhrase, s.dialogueParrotPhraseActive].join(' ')}>
              <p></p>
            </div>
          </div>
          <div className={s.kiwiPhrases}>
            <div className={[s.dialogueUserPhrase, s.dialogueUserPhraseActive].join(' ')}>
              <p></p>
            </div>
            <div className={[s.dialogueUserPhrase, s.dialogueUserPhraseActive].join(' ')}>
              <p></p>
            </div>
          </div>
          <div className={s.parrotKiwi}><img src={kiwi} className={s.kiwi}></img></div>
        </div>
        <div className={s.answerVariants}>
          {phrases.map(phrase => (
            <div className={s.answer}>{phrase.phrase}</div>
          ))}
        </div>
      </div>
    </div >
  );
}

export default Task9;